import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
//import { useDispatch } from "react-redux";
//import { registerUser } from "../../../_actions/user_action";

function Register() {
  // redux dispatch
  //const dispatch = useDispatch();

  // react hook state 
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const [Name, setName] = useState("");
  const [ConfirmPassword, setConfirmPassword] = useState("");
  //const [Age, setAge] = useState("");
  //const [Gender, setGender] = useState("male");
  //const [EduBackground, setEduBackground] = useState("");
  //const [Degree, setDegree] = useState("");
  const [isChecked, setIschecked] = useState(false);
  const [isRedundancy, setisRedundancy] = useState(false);

  const navigate = useNavigate();

  // handler 
  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
    setIschecked(false);
  };

  const onNameHandler = (event) => {
    setName(event.currentTarget.value);
  };

  const onPasswordHandler = (event) => {
    setPassword(event.currentTarget.value);
  };

  const onConfirmPasswordHandler = (event) => {
    setConfirmPassword(event.currentTarget.value);
  };

  // const onAgeHandler = (event) => {
  //   setAge(event.currentTarget.value);
  // };

  // const onGenderHandler = (event) => {
  //   setGender(event.currentTarget.value);
  // };

  // const onEduBackgroundHandler = (event) => {
  //   setEduBackground(event.currentTarget.value);
  // };

  // const onDegreeHandler = (event) => {
  //   setDegree(event.currentTarget.value);
  // };

  const onEmailRedundancyCheck = (event) => {
    event.preventDefault();
    fetch("http://localhost:4000/userinfo")
      .then((response) => response.json())
      .then((res) => {
        for (let i = 0; i < res.length; i++) {
          if (res[i]["email"] === Email) {
            setisRedundancy(true);
            return alert("   !");
          } else {
            setisRedundancy(false);
          }
        }
        if (!isRedundancy) alert("  !");
        setIschecked(true);
      });
  };

  const onSubmitHandler = (event) => {
    //       .
    event.preventDefault();

    if (Email === "") {
      return alert(" .");
    } else if (!isChecked) {
      return alert("   .");
    } else if (isRedundancy) {
      return alert("  .");
    } else if (Name === "") {
      return alert(" .");
    } else if (Password === "") {
      return alert(" .");
    } else if (Password !== ConfirmPassword) {
      return alert("   .");
    }
    // } else if (Age === "") {
    //   return alert(" .");
    // } else if (EduBackground === "") {
    //   return alert("  .");
    // } else if (Degree === "") {
    //   return alert("  .");
    // }

    let body = {
      email: Email,
      name: Name,
      password: Password,
      // age: Age,
      // gender: Gender,
      // eduBackground: EduBackground,
      // degree: Degree,
    };

    fetch("http://localhost:4000/register", {
      method: "post", // 
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(body),
    });

    console.log(body);

    alert("  .");

    navigate("/");
  };

  return (
    <form
      style={{ display: "flex", flexDirection: "column" }}
      onSubmit={onSubmitHandler}
    >
      <label></label>
      <input type="email" value={Email} onChange={onEmailHandler} />
      <button type="submit" onClick={onEmailRedundancyCheck}>
          
      </button>
      <label></label>
      <input type="text" value={Name} onChange={onNameHandler} />
      <label></label>
      <input type="password" value={Password} onChange={onPasswordHandler} />
      <label> </label>
      <input
        type="password"
        value={ConfirmPassword}
        onChange={onConfirmPasswordHandler}
      />
      {/* <label></label>
      <input
        type="number"
        value={Age}
        min="1"
        max="99"
        onChange={onAgeHandler}
      />
      <label></label>
      <select value={Gender} onChange={onGenderHandler}>
        <option value="male">male</option>
        <option value="female">female</option>
        <option value="other">other</option>
      </select>
      <label> </label>
      <input
        type="text"
        value={EduBackground}
        onChange={onEduBackgroundHandler}
      />
      <label> </label>
      <input type="text" value={Degree} onChange={onDegreeHandler} /> */}
      <button type="submit"> </button>
    </form>
  );
}

export default Register;
