import React, { useState, useEffect } from "react";
import { init, dispose } from "klinecharts";

import Layout from "../Layout";
import useNewData from "../hooks/useNewData";
import getInitialDataList from "../utils/getInitialDataList";
import getLanguageOption from "../utils/getLanguageOption";

const types = [
  { key: "candle_solid", text: "" },
  { key: "candle_stroke", text: " " },
  { key: "ohlc", text: "Bar  OHLC" },
  { key: "area", text: "Mountain" },
];
const CoinChart = () => {
  let chart;
  const [initialized, setInitialized] = useState(false);
  const newData = useNewData();

  useEffect(() => {
    chart = init("coin-chart");
    chart.setStyleOptions(getLanguageOption());
    const fetchData = async () => {
      const dataList = await getInitialDataList(1);
      chart.applyNewData(dataList);
      setInitialized(true);
    };
    fetchData();
    return () => {
      dispose("chart");
    };
  }, []);

  useEffect(() => {
    chart = init("coin-chart");
    if (initialized) {
      chart.updateData(newData);
    }
  }, [newData]);

  return (
    <Layout title="Bitcoin(BTC-KRW)   ">
      <div id="coin-chart" className="coin-chart" />
      <div className="chart-menu-container">
        {types.map(({ key, text }) => {
          return (
            <button
              key={key}
              onClick={(_) => {
                chart.setStyleOptions({
                  candle: {
                    type: key,
                  },
                });
              }}
            >
              {text}
            </button>
          );
        })}
      </div>
    </Layout>
  );
};

export default CoinChart;
